.card-skills-container {
  margin-top: 1.5vh;
  overflow: hidden;
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.1), 1px 1px 10px 1px rgba(0, 0, 0, 0.1);
  border-radius: 2vh;
  height: 36vh;
  cursor: pointer;
  transition-property: height;
  transition-duration: 300ms;
  background-color: white;
}
.card-skills-open {
  height: 6vh;
}
.card-skills-top {
  display: flex;
  align-items: center;
  height: 4vh;
  background-color: #03a9f4;
  flex-direction: row;
  color: white;
  padding: 1vh;
  padding-left: 3vh;
  padding-right: 3vh;

  font-size: 2.25vh;
  justify-content: space-between;
}
.card-skills-top:hover {
  background-color: #009fe9;
}
.chevron {
  fill: white;
  height: 2vh;
  transform: rotate(0deg);
  transition-property: transform;
  transition-duration: 300ms;
}
.chevron-open {
  transform: rotate(90deg);
}
.card-skills-content {
  padding: 1vh;
  padding-left: 3vh;
  padding-right: 3vh;
  color: #707070;
  font-size: 2vh;
  font-weight: 300;
}
