.card-education-container {
  display: flex;
  flex-direction: row;
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.1), 1px 1px 10px 1px rgba(0, 0, 0, 0.1);
  border-radius: 2vh;
  cursor: pointer;
  background-color: white;
  height: 8vh;
  padding: 3vh;
  color: #707070;
  margin-right: 1vh;
}
.education-logo {
  max-width: 8vh;
  max-height: 8vh;
  margin-right: 2vh;
}
.card-education-right {
  display: flex;
  flex-direction: column;
}
.card-education-title {
  font-size: 2.25vh;
}
.card-education-subtitle {
  font-size: 2vh;
}
.card-education-year {
  font-size: 2vh;
}
