.wrapper {
  /* border-style: solid;
  border-color: #a5a5a5;
  border-width: 25px;
  border-radius: 70px; */
  color: black;
  /* height: calc(100vh - 50px); */
  width: 55vh;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -o-user-select: none;
}
.background {
  background-color: #1e1e1e;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.circle-bottom {
  position: fixed;
  height: 50vh;
  width: 50vh;
  background-color: rgb(3, 169, 244);
  border-radius: 50%;
  bottom: -10vh;
  right: -20vh;
}
.circle-top:hover {
  top: -20vh;
  left: -30vh;
}
.circle-top {
  position: fixed;
  height: 50vh;
  width: 50vh;
  background-color: rgb(3, 169, 244);
  border-radius: 50%;
  top: -15vh;
  left: -25vh;
  transform: top, left;
  transition-duration: 500ms;
}
.square-bottom {
  position: fixed;
  top: 70vh;
  left: 20vh;
  height: 20vh;
  width: 20vh;
  background-color: rgb(255, 0, 0, 0);
  border-style: solid;
  border-width: 1px;
  border-color: rgba(255, 255, 255, 0.3);
  animation: rotate 30s linear infinite;
}
@keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}
.square-top {
  position: fixed;
  top: 10vh;
  right: 20vh;
  height: 20vh;
  width: 20vh;
  background-color: rgb(255, 0, 0, 0);
  border-style: solid;
  border-width: 1px;
  border-color: rgba(255, 255, 255, 0.3);
  transform: rotate(30deg);
}
