.card-experience-container {
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.1), 1px 1px 10px 1px rgba(0, 0, 0, 0.1);
  border-radius: 2vh;
  cursor: pointer;
  transition-property: height;
  transition-duration: 300ms;
  background-color: white;
  padding: 2vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  color: #707070;
  font-size: 2vh;
  justify-content: space-between;
  margin-right: 0.75vh;
  margin-top: 1vh;
  transition-property: box-shadow;
  transition-duration: 300ms;
}
.card-experience-container:hover {
  background-color: #f6f6f6;
}
.card-experience-container-open {
  box-shadow: 0 0 0 0 #03a9f4, 0px 0px 2px 0px #03a9f4;
}
.card-experience-left {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.experience-check {
  margin-right: 2vh;
  width: 7vh;
  max-height: 7vh;
  margin-right: 1vh;
}
.card-experience-top {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
}
.card-experience-title {
  font-size: 2.25vh;
}
.card-experience-title-open {
  color: #03a9f4;
}
.card-experience-bottom {
  margin-top: 2vh;
}
