.card-certification-container {
  margin-top: 1.5vh;
  overflow: hidden;
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.1), 1px 1px 10px 1px rgba(0, 0, 0, 0.1);
  border-radius: 2vh;
  cursor: pointer;
  transition-property: background-color, height;
  transition-duration: 200ms;
  background-color: white;
  height: 4vh;
  padding: 2vh;
  color: #707070;
}
.card-certification-container-open {
  background-color: rgb(221, 221, 221);
}
.card-certification-container:hover {
  height: 15vh;
}
.card-certification-top {
  display: flex;
  align-items: center;
  height: 4vh;
  flex-direction: row;
  font-size: 2.25vh;
  justify-content: space-between;
}
.card-certification-left {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.card-certification-bottom {
  margin-top: 1.5vh;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 2vh;
}
.card-certification-credential {
  color: #03a9f4;
}
.certification-logo {
  width: 8vh;
  margin-right: 2vh;
}
.card-certification-content {
  margin-top: 2vh;
  font-size: 2vh;
}
.checkCircle {
  height: 4vh;
}
