.skill-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 0.75vh;
  font-size: 2.25vh;
}
.skill-scale-on {
  height: 0.75vh;
  width: 0.75vh;
  background-color: #03a9f4;
  border-radius: 50%;
}
.skill-scale-off {
  height: 0.6vh;
  width: 0.6vh;
  background-color: white;
  border-radius: 50%;
  border-color: #03a9f4;
  border-width: 1px;
  border-style: solid;
}
.skill-scale-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 12vh;
}
