.card-header-container {
  overflow: hidden;
  background-color: white;
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.1), 1px 1px 10px 1px rgba(0, 0, 0, 0.1);
  border-radius: 2vh;
  padding: 3vh;
  height: 28.5vh;
  transition-property: height;
  transition-duration: 500ms;
}
a {
  text-decoration: none;
}
.card-header-top {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.card-header-name {
  color: #03a9f4;
  font-size: 3.5vh;
  font-weight: 500;
}
.card-header-role {
  color: #707070;
  font-size: 2vh;
}
.card-header-location {
  color: #707070;
  font-size: 2vh;
  font-weight: 100;
}
.card-header-profile {
  height: 11vh;
  border-radius: 50%;
}
.card-header-photo-container {
  display: flex;
  align-items: center;
}
.card-header-content {
  color: #707070;
  margin-top: 2vh;
  font-size: 2vh;
  font-weight: 300;
}
